<template>
  <div>
    <el-table
      :data="itemList"
      height="70%"
      style="position: absolute;top:10%;left: 10%;width: 80%;"
      :default-sort = "{prop: 'date', order: 'descending'}">
      <template slot="empty">
        
      </template>
      <el-table-column
        prop="code"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="close"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="open"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="high"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="low"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="volume"
        label=""
        align='center'>
      </el-table-column>
      <el-table-column
        prop="date"
        label=""
        align='center'
        sortable>
        <template slot-scope="scope">
          {{changeDateFormat(scope.row.date)}}
        </template>
      </el-table-column>
      <!--
      <el-table-column
        prop="ratio"
        label=""
        align='center'>
        <template slot-scope="scope">
          <a v-if="scope.row.ratio > 0" style="color: green">+{{float2(scope.row.ratio)}}%</a>
          <a v-else-if="scope.row.ratio < 0" style="color: red">-{{float2(scope.row.ratio)}}%</a>
          <a v-else-if="scope.row.ratio = 0" style="color: yellow">{{zero}}%</a>
        </template>
      </el-table-column>
      --->
      <el-table-column
        prop=""
        label=""
        align='center'>
        <template slot-scope="scope">
          <el-button @click="getDetail(scope.$index)" type="text" size="small">K</el-button>
          <el-button @click="getForecast(scope.$index)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="position: absolute;top:85%;left: 40%;width: 20%;height: 10%;">
      <span>:</span>
      <el-input v-model="page" @blur="getPage()" type="text" style="width:25%"></el-input>
      <span>/</span>
      <el-input v-model="totalPage" type="text" style="width:25%" :disabled="true" readonly="readonly"></el-input>
      <el-button type="primary" @click="getPage()" class="submitBtn" style="width:25%"></el-button>
    </el-row>
    <m-forecast v-bind:forecast="forecast" v-if="forecastVisible" :visible.sync = "forecastVisible"></m-forecast>
  </div>
</template>

<script>
import Forecast from '../main/forecast'
import { formatDate } from '../js/format.js'
import { isPostiveInteger } from '../js/validate.js'
export default {
  name: 'showToday',
  data () {
    return {
      title: '',
      itemList: [],
      page: 1,
      totalPage: 1,
      perPage: 20,
      totalNum: 1,
      forecastVisible: false,
      zero: 0.00
    }
  },
  components: {
    'm-forecast': Forecast
  },
  mounted: function () {
    this.getTotalPage()
    this.getPage()
  },
  methods: {
    getTotalPage () {
      let _this = this
      _this.$axios({
        method: 'post',
        url: '/get_codes_count'
      }).then(function (response) {
        console.log(response)
        _this.totalNum = response.data
        _this.totalPage = Math.ceil(_this.totalNum / _this.perPage)
      }).catch(function (res) {
      })
    },
    getPage () {
      let page = this.page
      page = Number.parseInt(page)
      let perPage = this.perPage
      let _this = this
      if (!isPostiveInteger(_this.page)) {
        _this.showMsg(_this, true, '', 'error')
      } else if (page > _this.totalPage) {
        _this.showMsg(_this, true, '', 'error')
      } else {
        let time = formatDate(new Date())
        let date = _this.judgeWeekend(time)
        _this.$axios({
          method: 'post',
          url: '/get_stock_list',
          data: {
            date: date,
            once: perPage,
            page: page
          }
        }).then(function (response) {
          console.log(response)
          _this.itemList = response.data
        }).catch(function (res) {
        })
      }
    },
    judgeWeekend (time) {
      time = new Date(time)
      if (time.getHours() < 18) {
        time.setDate(time.getDate() - 1)
      }
      if (time.getDay() === 6) {
        time.setDate(time.getDate() - 1)
        return this.changeDateFormat(time).substring(0, 10)
      } else if (time.getDay() === 0) {
        time.setDate(time.getDate() - 2)
        return this.changeDateFormat(time).substring(0, 10)
      } else {
        return this.changeDateFormat(time).substring(0, 10)
      }
    },
    getDetail (index) {
      let stockId = this.itemList[index].code
      this.$router.push({ path: '/StockDetails', query: { stockId: stockId } })
    },
    getForecast (index) {
      let stockId = this.itemList[index].stockId
      //
      this.forecastVisible = true
    },
    changeDateFormat (date) {
      let time = new Date(date)
      return formatDate(time)
    },
    float2 (num) {
      return parseFloat(num).toFixed(2)
    }
  }
}
</script>

<style scoped>

</style>
