/**
 * 
 * @param that this
 * @param close 
 * @param msg 
 * @param type: success/warning/info/error
 */
export const showMsg = (that, close, msg, type) => {
  that.$message({
    showClose: close,
    message: msg,
    type: type
  })
}
/**
 * 
 * @param that this
 * @param title 
 * @param msg 
 * @param type success/warning/info/error
 */
export const showMsgBox = (that, title, msg, type) => {
  that.$alert(msg, title, {
    confirmButtonText: '',
    type: type
  })
}
