<template>
  <div>
    <el-table
      :data="remarkList"
      height="60%"
      style="position: absolute;top:10%;left: 10%;width: 80%;"
      :row-style="rowStyle"
      title=""
      stripe
      :default-sort = "{prop: 'time', order: 'descending'}">
      <template slot="empty">
        
      </template>
      <el-table-column
        prop="date"
        label=""
        sortable>
        <template slot-scope="scope">
          <div>
            <a class="titleR">
              <a v-if="scope.row.link !== '' " @click="linkTo(scope.row.link)" class="link">{{scope.row.title}}</a>
              <a v-else-if="scope.row.link === '' ">{{scope.row.title}}</a>
            </a>
            <a class="timeR">{{scope.row.date}}</a>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="position: absolute;top:85%;left: 42%;width: 16%;height: 10%;">
      <span>:</span>
      <el-input v-model="page" @blur="getPage()" type="text" style="width:25%"></el-input>
      <span>/</span>
      <el-input v-model="totalPage" type="text" style="width:25%" :disabled="true" readonly="readonly"></el-input>
      <el-button type="primary" @click="getPage()" class="submitBtn" style="width:25%"></el-button>
    </el-row>
  </div>
</template>

<script>
import { formatDate } from '../js/format.js'
import { isPostiveInteger } from '../js/validate.js'
export default {
  name: 'showRemark',
  data () {
    return {
      remarkList: [],
      page: 1,
      totalPage: 25,
      perPage: 20,
      rowStyle: {
        height: '45px'
      }
    }
  },
  mounted: function () {
    this.getPage()
  },
  methods: {
    getPage () {
      let page = this.page
      page = Number.parseInt(page)
      let _this = this
      if (!isPostiveInteger(_this.page)) {
        _this.showMsg(_this, true, '', 'error')
      } else if (page > _this.totalPage) {
        _this.showMsg(_this, true, '', 'error')
      } else {
        _this.$axios({
          method: 'post',
          url: '/get_comments',
          data: {
            page: page
          }
        }).then(function (response) {
          console.log(response)
          _this.remarkList = response.data
          _this.json()
        }).catch(function (res) {
        })
      }
    },
    json () {
      let temp = this.remarkList
      this.remarkList = []
      for (let i = 0; i < temp.length; i++) {
        this.remarkList.push(JSON.parse(temp[i]))
      }
    },
    changeDateFormat (date) {
      let time = new Date(date)
      return formatDate(time)
    },
    linkTo (link) {
      window.open(link)
    }
  }
}
</script>

<style scoped>
  @import "../css/time.css";
</style>
