<template>
  <div>
    <div>
      <!--  -->
      <el-date-picker
        ref="beginDate"
        v-model="beginDate"
        type="date"
        placeholder="">
      </el-date-picker>
      &nbsp;
      <!--  -->
      <el-select ref="gap" v-model="dateGap" placeholder="" value="">
        <el-option
          v-for="item in selectedOption"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
        <el-button type="primary" @click="getStockDetails()"></el-button>
    </div>
    <div id="chart"></div>
  </div>
</template>

<script>
import { formatDate } from '../js/format.js'
let time = []
let MA_5 = []
let MA_10 = []
let MA_20 = []
let MA_30 = []
let K = []
let Volume = []
export default {
  name: 'StockDetails',
  mounted: function () {
    // 
    // Vue
    this.getParams()
    this.init()
  },
  methods: {
    getParams () {
      this.stockId = this.$route.query.stockId
    },
    init () {
      // this.beginDate = new Date()
      let now = new Date()
      now.setDate(now.getDate() - 1)
      this.beginDate = now
      this.getStockDetails() // 
    },
    async getStockDetails () {
      // restestData
      // this.getTime(testData)
      // this.getK(testData)
      let stockId = this.stockId
      let date = this.judgeWeekend(new Date(this.beginDate))
      let gap = this.$refs.gap.value
      let testData = []
      let _this = this
      _this.$axios({
        method: 'post',
        url: '/fetch_k',
        data: {
          code: stockId, // 'sz.000001'
          time_start: date, // '2019-05-17'
          gap: gap // 'd'
        }
      }).then(function (response) {
        console.log(response)
        testData = response.data
        _this.next(testData)
      }).catch(function (res) {
      })
    },
    async next (testData) {
      await this.getTime(testData)
      await this.getK(testData)
    },
    async getK (data) {
      console.log(data)
      for (let i = 0; i < data.length; i++) {
        let temp = []
        temp[0] = data[i].open
        temp[1] = data[i].close
        temp[2] = data[i].low
        temp[3] = data[i].high
        K[i] = temp
        Volume[i] = data[i].volume
      }
      this.myOption.series[1].data = await this.calculateMA(5)
      this.myOption.series[2].data = await this.calculateMA(10)
      this.myOption.series[3].data = await this.calculateMA(20)
      this.myOption.series[4].data = await this.calculateMA(30)
      console.log(this.myOption)
      this.$chart.showKLine('chart', this.myOption)
    },
    calculateMA (k) {
      let tempMA = []
      for (let i = 0; i < K.length; i++) {
        if (i === k - 1) {
          tempMA[i] = 0
          for (let j = 0; j < k; j++) {
            tempMA[i] = K[i][1] + tempMA[i]
          }
          tempMA[i] = tempMA[i] / k
        } else if (i > k - 1) {
          tempMA[i] = (tempMA[i - 1] * k + K[i][1] - K[i - k][1]) / k
        }
      }
      return tempMA
    },
    getTime (data) {
      for (let i = 0; i < data.length; i++) {
        time[i] = this.changeDateFormat(data[i].date)
      }
    },
    /*
    getK () {
      let stockId = this.stockId
      console.log(stockId)
      let time = formatDate(new Date())
      let date = this.judgeWeekend(time)
      let gap = this.gap
      let _this = this
      _this.$axios({
        method: 'post',
        url: '/fetch_k',
        data: {
          code: stockId, // 'sz.000001'
          time_start: date, // '2019-05-17'
          gap: gap // 'd'
        }
      }).then(function (response) {
        console.log(response)
        _this.KData = response.data
        _this.print()
      }).catch(function (res) {
      })
    },
    */
    judgeWeekend (time) {
      if (time.getHours() < 22) {
        time.setDate(time.getDate() - 1)
      }
      if (time.getDay() === 6) {
        time.setDate(time.getDate() - 1)
        return this.changeDateFormat(time).substring(0, 10)
      } else if (time.getDay() === 0) {
        time.setDate(time.getDate() - 2)
        return this.changeDateFormat(time).substring(0, 10)
      } else {
        return this.changeDateFormat(time).substring(0, 10)
      }
    },
    changeDateFormat (date) {
      let time = new Date(date)
      return formatDate(time)
    }
  },
  data () {
    return {
      stockId: '',
      beginDate: '2019-04-01',
      dateGap: '5',
      KLength: 0,
      selectedOption: [
        {
          value: '5',
          label: '5'
        }, {
          value: '15',
          label: '15'
        }, {
          value: '30',
          label: '30'
        }, {
          value: '60',
          label: '60'
        }, {
          value: 'd',
          label: '1'
        }, {
          value: 'm',
          label: '1'
        }, {
          value: 'y',
          label: '1'
        }
      ],
      myOption: {
        title: {
          text: 'K'
        },
        legend: {
          bottom: 10,
          left: 'center',
          data: ['K', 'MA5', 'MA10', 'MA20', 'MA30']
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          formatter: function (params) {
            let res = ': ' + params[0].name
            res += '<br/>   : ' + params[0].value[0]
            res += '<br/>   : ' + params[0].value[1]
            res += '<br/>   : ' + params[0].value[3]
            res += '<br/>   : ' + params[0].value[2]
            res += '<br/>   : ' + params[1].value
            return res
          }
        },
        toolbox: {
          show: false,
          feature: {
            mark: {
              show: true
            },
            dataZoom: {
              show: true,
              yAxisIndex: false
            },
            dataView: {
              show: true, readOnly: false
            },
            magicType: {
              show: true,
              type: ['line', 'bar']
            },
            restore: {
              show: true
            },
            saveAsImage: {
              show: true
            }
          }
        },
        axisPointer: {
          link: {
            xAxisIndex: 'all'
          },
          label: {
            backgroundColor: '#777'
          }
        },
        dataZoom: [
          {
            type: 'inside',
            xAxisIndex: [0, 1],
            realtime: true,
            start: 50,
            end: 100
          },
          {
            show: true,
            type: 'slider',
            xAxisIndex: [0, 1],
            realtime: true,
            top: '85%',
            start: 50,
            end: 100
          }
        ],
        grid: [
          {
            left: '10%',
            right: '8%',
            height: '50%'
          },
          {
            left: '10%',
            right: '8%',
            top: '63%',
            height: '16%'
          }
        ],
        xAxis: [
          {
            type: 'category',
            boundaryGap: true,
            scale: true,
            axisTick: {
              onGap: false
            },
            splitLine: {
              show: false
            },
            axisPointer: {
              z: 100
            },
            data: time
          },
          {
            type: 'category',
            scale: true,
            gridIndex: 1,
            boundaryGap: true,
            axisTick: {
              onGap: false
            },
            splitLine: {
              show: false
            },
            axisLabel: {
              show: false
            },
            data: time
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: 'K',
            scale: true,
            boundaryGap: [0.01, 0.01]
          },
          {
            type: 'value',
            scale: true,
            boundaryGap: [0.01, 0.01],
            gridIndex: 1,
            axisLabel: {
              show: false
            },
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            name: 'K',
            type: 'k',
            data: K
          },
          {
            name: 'MA5',
            type: 'line',
            smooth: true,
            data: MA_5
          },
          {
            name: 'MA10',
            type: 'line',
            smooth: true,
            data: MA_10
          },
          {
            name: 'MA20',
            type: 'line',
            smooth: true,
            data: MA_20
          },
          {
            name: 'MA30',
            type: 'line',
            smooth: true,
            data: MA_30
          },
          {
            name: '',
            xAxisIndex: 1,
            yAxisIndex: 1,
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#7fbe9e'
              },
              emphasis: {
                color: '#140'
              }
            },
            data: Volume
          }
        ]
      }
    }
  }
}
</script>

<style scoped>
  @import "../css/chart.css";
</style>
